import React from 'react';

function App() {
  const w = 8;
  const h = 5;

  const maxW = 6;
  const maxH = 4;

  let cols = [...new Array(h)].map(() => new Array(w).fill(false));
  let tiles = [];

  for(let y = 0; y < cols.length; y++){
    const row = cols[y];

    for(let x = 0; x < row.length; x++){
      const cell = row[x];

      if(cell === false){
        let avilableWidth = 0;
        
        for(let j = x; j < w; j++){
          if(row[j] === false){
            avilableWidth++;
          } else break;
        }

        const randomW = randomTiles(maxW, avilableWidth + x, x);
        const randomH = randomTiles(maxH, h, y);

        tiles.push({x: [x, randomW], y: [y, randomH]});

        for(let i = y; i < randomH + y; i++){
          cols[i].splice(x, randomW, ...new Array(randomW).fill(true));
        }
      }
    }
  }

  console.log('tiles',tiles);
  const colors = [
    "fff",
    "000",
    "ffff00"
  ]

  return (
    <main>
      {tiles.map(tile => {
        const color = colors[Math.floor(Math.random() * colors.length)];

        const style = {
          gridColumn: `${tile.x[0] + 1} / span ${tile.x[1]}`,
          gridRow: `${tile.y[0] + 1} / span ${tile.y[1]}`,
          backgroundColor: "#" + color
        }

        return(
          <div 
            className="tile" 
            style={style}
          />
        );
      })}
    </main>
  );
}

const randomTiles = (maxTiles, totalLength, curr) => {
  const tilesLeft = totalLength - curr;
  const largestPossible = Math.min( maxTiles, tilesLeft );
  const random = Math.ceil( largestPossible * Math.random() );

  return random;
}

export default App;
